import styled from 'styled-components'

export const Heading = styled.h2`
  font-size: 2.25rem;
  text-align: center;
`

export const SubHeading = styled.p`
  font-size: 1.125rem;
  text-align: center;
  margin-bottom: 3rem;
`

export const Feature = styled.div`
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;

  &:nth-child(-n+3){
    margin-bottom: 1.5rem;
  }

  @media (min-width: 1024px){
    flex-basis: 25%;
    max-width: 25%;
    &:nth-child(-n+3){
      margin-bottom: 0;
    }
  }
`

export const Title = styled.h3`
  font-size: 20px;
  margin: 20px 0 10px;
`

export const Description = styled.p`
  font-size: 1rem;
  margin-bottom: 1rem;
`

export const Card = styled.div`
  position: relative;
  flex-basis: 100%;
  color: rgb(0, 123, 255);
  height: 328px;
  border-radius: 0.25rem;

  

  background: ${({ image }) => `url(${image})`};
  background-position: center;
  background-size: auto;
  background-repeat: no-repeat;
  cursor: pointer;
  

  &:nth-child(-n+2){
    margin-bottom: 1.5rem;
  }

  &:nth-child(3){
   margin-bottom: 1.5rem;
  }

  &::before {
    content: '';
    opacity: 0;
    position: absolute;
    z-index: 1;
    border-radius: 0.25rem;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    transition: all 0.5s ease;
  }

  &::after {
    content: '${({ content }) => content}';
    position: absolute;
    width: 100%;
    z-index: 2;
    padding: 15px 20px;
    bottom: 0;
    color: #000;
    font-family: 'rubikregular' sans-serif;
    font-size: 1.3125rem;
    text-align: center;
    border-radius: 0 0 0.25rem 0.25rem;
    transition: all 0.3s ease;
  }

  &:hover {
   &::before {
     opacity: 1;
   }
   &::after {
    padding-left: 28px;
   }
  }

  @media (min-width: 768px){
    flex-basis: calc(50% - 15px);
    height: 282px;

    &:nth-child(3){
      margin-bottom: 0;
    }
  }

  @media (min-width: 1024px){
    height: 350px;
    
  }
`

export const Cards = styled.div`
  display: flex;
  flex-basis: 100%;
  flex-wrap: wrap;
  background-color: #000;
  justify-content: space-between;
  padding-right: 400px;
  padding-left: 20px;

  @media (min-width: 1024px){
    flex-basis: 58.333333%;
  }

  @media (min-width: 1200px){
    flex-basis: 50%;
  }
`

export const Rows = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: ${({ justify }) => justify ?? "flex-start"};
  margin-left: -15px;
  margin-right: -15px;
`