import React, { useState } from "react"



import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { Wrapper, Contents, Row, ButtonOutlinePrimary, Modal } from "../../common"
import {
  IFrame,
  Video,
  Placeholder,
  Info,
  Heading,
  SubHeading,
  Icon,
  List,

} from "./styles"


import comofunciona from "assets/images/como-funciona.png"

function HowDoesItWork() {
  const [showModal, setShowModal] = useState(false)
  const closeModal = () => setShowModal(false)
  const openModal = () => setShowModal(true)

  return (
    <Wrapper bgColor="#f5f5f5">
      <Contents>
        <Row>
          <Video onClick={openModal}>
            <Placeholder src={comofunciona} alt="Cambio Online Demo Video" />
            
          </Video>
          <Info>
            <Heading>Como funciona?</Heading>
            <SubHeading>
              <Wrapper bgColor="#f5f5f5">
                Clique em Enviar dinheiro Envie para o país, a moeda e a quantidade de dinheiro que deseja enviar.
                
              </Wrapper>
              <a href="/signup">
              <ButtonOutlinePrimary>ENVIAR  DINHEIRO</ButtonOutlinePrimary>
              </a>
              <Wrapper bgColor="#f5f5f5">
                Faça seu cadastro
                <br />
                Realize seu cadastro gratuitamente ou, se você já tem cadastro, faça o login. 
              </Wrapper>
              <Wrapper bgColor="#f5f5f5">
              Cadastre também a pessoa para quem você deseja enviar dinheiro para o exterior. 
              </Wrapper>
       
            </SubHeading>
            <List>
              <li>
                <Icon>
                  <FontAwesomeIcon icon="check" size="1x" />
                </Icon>
                
                Registrar Conta
              </li>
              <li>
                <Icon>
                  <FontAwesomeIcon icon="check" size="1x" />
                </Icon>
                
                Receba e envie pagamentos de todo o mundo
              </li>
              <li>
                <Icon>
                  <FontAwesomeIcon icon="check" size="1x" />
                </Icon>
                
                Seus fundos serão transferidos para sua conta bancária local
              </li>
            </List>
           <a href="/signup">
            <ButtonOutlinePrimary>ABRA UMA CONTA</ButtonOutlinePrimary>
          </a>
          </Info>
        </Row>
        <br />
       <p>Ele diz respeito a uma lista com classificações, identificando o motivo do dinheiro estar sendo enviado para outro país.</p> 
       <p>Confirme os dados do pedido e faça o pagamento via Ted ou boleto bancário.</p>
       <p>Aproveite a ISENÇÃO na Tarifa Administrativa na sua primeira Remessa e Pagamento Internacional.</p>
      </Contents>
    </Wrapper>
  )
}

export default HowDoesItWork
