import React from "react"
import features from "./features"
import { Wrapper, Contents, Row } from "../../common"
import { Heading, SubHeading, Card, WrapperCard } from "./styles"

function WhyChoose() {
  return (
    <Wrapper>
      <Contents>
        <Heading>Por que você deve escolher Cambio Online?</Heading>
        <SubHeading>Os 6 principais motivos pelos quais você deve usar um Cambio Online para gerenciar seu dinheiro.</SubHeading>
        <Row>
        
        {features.map((features, index) => (
          <Card 
            key={index}
            image={features.image}
  
            content={features.description}
          />
        ))}
     
        </Row>
       </Contents>
    </Wrapper>
  )
}

export default WhyChoose
