import React from "react";
import { LinkNav, SignupButton} from "./styles";



function LoggedIn({ onClick }) {
  return (
    <>

      <LinkNav to="/receber" aria-label="Receber">
        <SignupButton>Receber Dinheiro</SignupButton>
      </LinkNav>

      <LinkNav to="/send-recend-home" aria-label="Enviar">
        <SignupButton>Enviar Dinheiro </SignupButton>
      </LinkNav>
      
    </>
  );
}

export default LoggedIn;
