import React from "react";
import { LinkNav, SignoutButton,SignupButton } from "./styles";

function userNotLoggedIn() {
  return (
    <>
      <LinkNav to="/login" aria-label="Your account settings">
        <SignupButton>LOGIN</SignupButton>
      </LinkNav>

      <LinkNav to="/signup" aria-label="Your account settings">
        <SignoutButton>REGISTRAR</SignoutButton>
      </LinkNav>
    </>
  );
}

export default userNotLoggedIn;
